
import XProgress from 'vux/src/components/x-progress/index.vue'

export default {
components: {
XProgress
},
created () {
setTimeout(() => {
this.addTime()
}, 200)
},
data () {
return {
precent: 0,
time: ''
}
},
methods: {
addTime () {
this.time = setInterval(() => {
if (this.precent < 100) {
this.precent++
} else {
clearInterval(this.time)
setTimeout(() => {
this.$router.push({ path: '/my' })
}, 1000)
}
}, 50)
}
}
}



